import React from "react";
import styles from './CircleLinks.module.scss';
import CircleLink, {ICircleLinkProps} from './components/circleLink';

interface ICircleLinksProps {
  className?: string;
  circles: ICircleLinkProps[];
}

const CircleLinks = (props: ICircleLinksProps) => {
  return (
    <div className={`${styles.CircleLinks} ${props.className}`.trim()}>
      {props
        .circles
        .map((circle, index) => <CircleLink key={index} {...circle}/>)
      }
    </div>
  );

};

CircleLinks.defaultProps = {
  className: '',
  circles: [],
};

export default CircleLinks;
