import React from "react";
import styles from './ExpandIcon.module.scss';
import ExpandLessRoundedIcon from '@material-ui/icons/ExpandLessRounded';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';

interface IExplanationCardProps {
  expanded?: boolean
  onClick?: () => void;
}

const ExpandIcon = (props: IExplanationCardProps) => {
  return (
    <span className={styles.expandIcon} onClick={props.onClick}>
      {props.expanded ? <ExpandMoreRoundedIcon/> : <ExpandLessRoundedIcon/>}
    </span>
  );

};

ExpandIcon.defaultProps = {
  className: '',
  onClick: () => {
  }
};

export default ExpandIcon;
