import React, {useState} from "react";
import styles from './ExplanationCard.module.scss';
import ExpandIcon from './components/expandIcon';

interface IExplanationCardProps {
  title?: string;
  content?: string;
  children?: any;
  className?: string;
}

const ExplanationCard = (props: IExplanationCardProps) => {
  const [isCardContentCollapsed, setCardContentCollapse] = useState(true);
  return (
    <div className={`${styles.cardContainer} ${props.className}`}>
      <p className={styles.titleContainer}>
        {props.title}
        <ExpandIcon onClick={() => setCardContentCollapse(!isCardContentCollapsed)} expanded={isCardContentCollapsed}/>
      </p>
      <p className={`${styles.contentContainer} ${isCardContentCollapsed ? styles.collapsed : ''}`.trim()}>
        {props.content}
        {props.children}
      </p>
    </div>
  );

};

ExplanationCard.defaultProps = {
  className: '',
};

export default ExplanationCard;
