import React from "react";
import {IProduct} from 'data/interfaces/products';
import AdDiscount
  from "gui/components/listPage/components/ad/components/adDiscount/AdDiscount";
import CardGiftCardIcon from '@material-ui/icons/CardGiftcard';
import {getVoucherName} from 'data/redux/selectors/productSelector';

interface IRollingAd {
  product: IProduct;
}

/**
 * Makes sure to strip any sliced words
 * @param input
 * @param from
 * @param to
 */
const sliceExcludeSlicedWords = (input, from: number = 0, to: number = 15) => {
  if (input.length > to) {
    const i = input.split(' ').map(s => s.trim()).filter(s => s);
    input = i.join(' ');
    const slicedInput = input.slice(from, to);
    const i2 = slicedInput.split(' ');
    if (i[i2.length - 1] !== i2[i2.length - 1]) {
      return i2.slice(0, i2.length - 2).join(' ');
    }
    return slicedInput;
  }
  return input;
}

const RollingAdVoucher = ({product}: IRollingAd) => (<>
  <CardGiftCardIcon/> {sliceExcludeSlicedWords(getVoucherName(product))}
  <AdDiscount originalPrice={product.voucherDetails.voucherBalance}
              askingPrice={product.saleDetails.askingPrice}
              style={{marginRight: 10}}
  />
</>);


RollingAdVoucher.defaultProps = {};

export default React.memo(RollingAdVoucher);
