import DealAndGoBuyerCard from 'gui/pages/mainRouter/pages/home/components/dealAndGoBuyerCard/DealAndGoBuyerCard';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';
import {
  flights_TYPE,
  hotelDetails,
  hotels_TYPE,
  PRODUCT_STATUS_PUBLISHED,
  ticketDetails,
  vacations_TYPE,
  voucherDetails,
  vouchers_TYPE
} from 'data/redux/constants/productsConstants';
import {capitalize} from '@material-ui/core';
import ProductsRollerContainer from 'data/redux/containers/productsRollerContainer/ProductsRollerContainer';
import {linksMap} from 'data/utils/constants';
import CombinedTypeResultsContainer
  from 'data/redux/containers/combinedTypeResultsContainer/CombinedTypeResultsContainer';
import CircleLinks from './components/circleLinks';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';

interface IHomeProps {

}

const discountType = (type: string) => {

  switch (type) {
    case flights_TYPE:
    case  vacations_TYPE:
      return ticketDetails;
    case hotels_TYPE:
      return hotelDetails;
    case vouchers_TYPE:
      return voucherDetails;
  }
}

const resultsQuery: any = (queryOptions, {currentValues, ...props}) => {
  return [
    {
      status: {
        $in: [
          PRODUCT_STATUS_PUBLISHED,
        ],
      },
      $sort: {
        askingPrice: 1,
        updatedAt: -1
      },
      $limit: 2,
      name: props.type.slice(0, -1),
    },
    {
      status: {
        $in: [
          PRODUCT_STATUS_PUBLISHED,
        ],
      },
      $sort: {
        discount: -1
      },
      $modify: `discount${capitalize(discountType(props.type))}`,
      $limit: 2,
      name: props.type.slice(0, -1),
    }
  ];
};

