import React from 'react';
import styles from './AdDiscount.module.scss';
import percentage from 'calculate-percentages';

interface IAdDiscountProps {
  originalPrice: number;
  askingPrice: number;
  style?: any;
}

const AdDiscount = (props: IAdDiscountProps) => (
  <span className={styles.adDiscount} style={props.style}>
    {[`${100 - Math.floor(percentage.calculate(props.askingPrice, props.originalPrice))}%`, 'הנחה'].join(" ")}
  </span>

);

AdDiscount.defaultProps = {
  label: '',
  price: 0,
  currency: 0,
};
export default AdDiscount;
