import {connect} from 'react-redux'
import {typeProductSelector} from 'data/redux/selectors/productSelector';
import ProductsRoller from 'gui/pages/mainRouter/pages/home/components/productsRoller';
import shuffleSeed from 'shuffle-seed';
import uniqWith from 'uniq-with';

const mapStateToProps = (state, {type, limit, queryOptions, queryResultPreProcess}) => {

  const products = uniqWith((product1, product2) => product1.id === product2.id, shuffleSeed.shuffle(["flights", "hotels", "vouchers", "vacations"].map(type => typeProductSelector(type)(state)).flat(), "my super secret seed"));
  const duplicatedProducts = products;
  if (products.length > 0) {
    let cnt = 0;
    while (duplicatedProducts.length < 16) {
      duplicatedProducts.push(duplicatedProducts[cnt]);
    }
  }
  return {
    products: duplicatedProducts,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

const ProductsRollerContainer = connect(mapStateToProps, mapDispatchToProps)(ProductsRoller) as any;

export default ProductsRollerContainer;
