import React from "react";
import styles from './CircleLink.module.scss';
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer';

export interface ICircleLinkProps {
  to?: string;
  src?: string;
  label?: string;
  soon?: boolean;
}

const CircleLink = (props: ICircleLinkProps) => {
  return (
    <LinkBlockContainer
      className={`${styles.categoryCircle} ${props.soon ? styles.soon : ''}`.trim()}
      brand
      to={props.to}
      src={props.src}
    >
      <h4 className={styles.label}>{props.label}</h4>
      {props.soon ? <h5 className={styles.soonlabel}>בקרוב</h5> : null}
    </LinkBlockContainer>
  );

};

CircleLink.defaultProps = {};

export default CircleLink;
