import React, {useState} from "react";
import {IProduct} from 'data/interfaces/products';
import RollingAd from './components/rollingAd';
import Marquee from "react-marquee-slider";
import VisibilitySensor from '@yosefsolutions/react-visibility-sensor-v2';

const MM: any = Marquee;


interface IProductsRoller {
  products: IProduct[];
}


const ProductsRoller = (props: IProductsRoller) => {
  const [roll, setRoll] = useState(true);
  return (
    <div key={props.products.length}
         onMouseEnter={() => setRoll(false)}
         onMouseLeave={() => setRoll(true)}>
      <VisibilitySensor onChange={(isVisible) => setRoll(isVisible)}>
        <MM velocity={roll ? (Math.ceil(window.innerWidth / 182) || 1) : 0} minScale={0.7} resetAfterTries={200}>
          {props.products.map((product) => (
            <RollingAd key={product.id} product={product}/>
          ))}
        </MM>
      </VisibilitySensor>
    </div>
  );
};


ProductsRoller.defaultProps = {
  toggleTab: (selectedTab) => {
    
  },
  activeTab: 1
};

export default React.memo(ProductsRoller);
