import React from "react";
import {IProduct} from 'data/interfaces/products';
import AdDiscount
  from "gui/components/listPage/components/ad/components/adDiscount/AdDiscount";
import OpenGiftIcon from 'gui/components/icons/OpenGiftIcon';
import {generateCountryNameFromProduct} from 'gui/components/listPage/components/ad/components/adVacation';

interface IRollingAd {
  product: IProduct;
}

export const generateAdVacationTitle = (product: IProduct) => {
  return ['חופשה ל', generateCountryNameFromProduct(product)].join("");
}

const RollingAdVacation = ({product}: IRollingAd) => (<>
  <OpenGiftIcon/> {generateAdVacationTitle(product)}
  <AdDiscount originalPrice={product.ticketDetails.currentAirlineAllTicketsPrice}
              askingPrice={product.saleDetails.askingPrice}
              style={{marginRight: 10}}
  />
</>);


RollingAdVacation.defaultProps = {};

export default React.memo(RollingAdVacation);
