import React from "react";
import {IProduct} from 'data/interfaces/products';
import FlightRoundedIcon from '@material-ui/icons/FlightRounded';
import AdDiscount from "gui/components/listPage/components/ad/components/adDiscount/AdDiscount";

interface IRollingAd {
  product: IProduct;
}

const RollingAdFlight = ({product}: IRollingAd) => (<>
  <FlightRoundedIcon/> {product.ticketDetails.originAirport.city} - {product.ticketDetails.destinationAirport.city}
  <AdDiscount originalPrice={product.ticketDetails.currentAirlineAllTicketsPrice}
              askingPrice={product.saleDetails.askingPrice}
              style={{marginRight: 10}}
  />
</>);


RollingAdFlight.defaultProps = {};

export default React.memo(RollingAdFlight);
