import React from "react";
import styles from './RollingAd.module.scss';
import {IProduct} from 'data/interfaces/products';
import {
  hotelDetails,
  identifyProductCategoryType,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';
import RollingAdFlight from './components/rollingAdFlight/RollingAdFlight';
import LinkBlockContainer from "data/redux/containers/linkBlockContainer/LinkBlockContainer";
import {generateSluggedAdUrl} from 'data/redux/actions/guiActions';
import RollingAdHotel from './components/rollingAdHotel/RollingAdHotel';
import RollingAdVacation from './components/rollingAdVacation';
import RollingAdVoucher
  from 'gui/pages/mainRouter/pages/home/components/productsRoller/components/rollingAd/components/rollingAdVoucher/RollingAdVoucher';

interface IRollingAd {
  product: IProduct;
}

const selectProductType = (product: IProduct) => {
  switch (identifyProductCategoryType(product)) {
    case ticketDetails:
      return (<RollingAdFlight product={product}/>);
    case hotelDetails:
      return (<RollingAdHotel product={product}/>);
    case vacationDetails:
      return (<RollingAdVacation product={product}/>)
    case voucherDetails:
      return (<RollingAdVoucher product={product}/>);
  }
  return (null);
}

const RollingAd = ({product}: IRollingAd) => (
  <LinkBlockContainer className={styles.rollingAd} to={generateSluggedAdUrl(product)}>
    <span className={styles.rollingAdInner}>
      {selectProductType(product)}
      </span>
    <span className={`${styles.rollingAdDivider} ${styles.rollingAdDividerLeft}`}/>
  </LinkBlockContainer>);

RollingAd.defaultProps = {};

export default React.memo(RollingAd);
