import React from "react";
import {IProduct} from 'data/interfaces/products';
import HotelRoundedIcon from '@material-ui/icons/HotelRounded';
import AdDiscount
  from "gui/components/listPage/components/ad/components/adDiscount/AdDiscount";
import {generateNameWithDelimeter} from 'data/utils/calculators';

interface IRollingAd {
  product: IProduct;
}

const generateHotelDestination = product => [
  generateNameWithDelimeter(", ",
    product.hotelDetails.hotel.city,
    product.hotelDetails.hotel.countryShort,
  ),
  generateNameWithDelimeter("",
    product.hotelDetails.hotel.name.slice(0, 15)
  )
].join(" - ");


const RollingAdHotel = ({product}: IRollingAd) => (<>
  <HotelRoundedIcon/> {generateHotelDestination(product)}
  <AdDiscount originalPrice={product.hotelDetails.totalHotelPaidPrice}
              askingPrice={product.saleDetails.askingPrice}
              style={{marginRight: 10}}
  />
</>);


RollingAdHotel.defaultProps = {};

export default React.memo(RollingAdHotel);
